.Game {
    position: relative;
    border-radius: 20px;
    width: 300px;
    height: 300px;
    background: #585858;
    display: flex;
    flex-wrap: wrap;
    padding: 2px;
}
.field {
    background: darkgray;
    width: 33.33333%;
    height: 33.33333%;
    cursor: pointer;
    transform: scale(0.95);
}
.field:nth-child(1) {border-radius: 20px 0 0 0;}
.field:nth-child(3) {border-radius: 0 20px 0 0;}
.field:nth-child(7) {border-radius: 0 0 0 20px;}
.field:nth-child(9) {border-radius: 0 0 20px 0;}
.circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.cross {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.cross img, .circle img {
    width: 75%;
    display: block;
}
.winner{
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #642B73;
    background: -webkit-linear-gradient(to right, #C6426E, #642B73);
    background: linear-gradient(to right, #C6426E, #642B73);
}
.standoff{
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #642B73;
    background: -webkit-linear-gradient(to right, #C6426E, #642B73);
    background: linear-gradient(to right, #C6426E, #642B73);
}
h1{
    text-align: center;
    text-transform: uppercase;
    background: darkgrey;
    width: 100%;
    margin-top: 20px;
    border-radius: 20px;
    border: 4px solid #585858;
    font-size: 20px;
    padding: 5px 0px;
    box-sizing: border-box;
}
.record-table{
    background: darkgrey;
    width: 100%;
    margin-top: 20px;
    border-radius: 20px;
    border: 4px solid #585858;
    text-align: center;
}
.record-table>div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px 15px 10px;
}
.record-table>p{
    margin-bottom: 0;
    font-weight: bold;
    text-transform: uppercase;
}
